export default {
	data() {
		return {
			userInfo: {
				clientNo: null,
				userName: '未登录',
				avaterUrl: '/static/icon/smile-fill.png'
			},
			vipInfo: {
				
			},
			levelpercent: "",
			loading:false,
			acctDetail: {},
			pay: 999,
			income: 555,
			isLogin: false,
			assets: 100000,
			lastIncome: -25,
			userLevel: {},
			messageList: [{
				cuIcon: 'redpacket',
				color: 'alive',
				number: '--',
				badge: 0,
				url: "/pages/bank_card/index/index",
				name: '银行卡'
			}, {
				cuIcon: 'refund',
				color: 'alive',
				number: '--',
				badge: 0,
				name: '卡券'
			}, {
				cuIcon: 'present',
				color: 'alive',
				number: '--',
				badge: 0,
				name: '积分'
			}],
			gridCol: 3,
			isVip: false
		}
	},
	onShow(e) {
		let _this = this;
		
		// 获取用户信息
		_this.isLogin = uni.getStorageSync('isLogin');
		uni.getStorage({
			key: 'vipInfo',
			success: (res) => {
				_this.vipInfo = res.data;
				if(_this.vipInfo.length > 0){
					_this.isVip = true;
				}
			}
		});
		
		
	},
	methods: {
		change() {
			let _this = this;

			if(uni.getStorageSync('isLogin')){
				uni.navigateTo({
					url: '/pages/personal/information'
				});					
			}else{
				uni.navigateTo({
					url: '/pages/login/login'
				});					
			}
			
		},
		nextStep(url){
			let _this = this;
			console.log("进来了");
			console.log(url);
			uni.navigateTo({
				url: url
			});
		},
		toVip () {
			uni.navigateTo({
				url: "/pages/user/vip/vip"
			})
		}
	},
}
